* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-black: #1d1d1f;
  --primary-white: #f8f8f8;
  --accent-color: #f6ff7d;
}

body {
  width: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(150deg, #f8f8f8 0%, #f8e7ce 100%);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.disabled {
  pointer-events: none;
  visibility: hidden;
  display: none;
}

.header {
  width: 100%;
  height: 80px;
  background-color: var(--primary-black);
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  display: flex;
}

.search-form {
  display: flex;
}

.search-input {
  border: 2px solid var(--primary-white);
  color: var(--primary-white);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50px 0 0 50px;
  outline: none;
  padding: 7px 15px;
  font-size: 1.2rem;
}

.search-input:hover, .search-input:focus, .search-input:active {
  border: 2px solid var(--accent-color);
}

.search-submit {
  color: var(--primary-black);
  background-color: var(--primary-white);
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 0 50px 50px 0;
  outline: none;
  padding: 7px 12px 7px 7px;
  font-size: 1.2rem;
}

.search-submit:hover, .search-submit:focus, .search-submit:active {
  background-color: var(--accent-color);
}

.main {
  flex-direction: column;
  display: flex;
}

.gallery {
  width: 88%;
  max-width: 1700px;
  border-radius: 15px;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 5px;
  margin: auto;
  padding: 20px;
  display: flex;
}

.photo-card {
  background-color: var(--primary-black);
  color: var(--primary-white);
  border: 2px solid var(--primary-white);
  border-radius: 0 0 10px 10px;
  flex-direction: column;
  flex-basis: calc(25% - 5px);
  justify-content: space-between;
  display: flex;
}

.photo-card img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.info {
  justify-content: space-evenly;
  align-items: center;
  padding: 10px;
  display: flex;
}

.info-item {
  border-bottom: 2px solid var(--primary-white);
  flex-direction: column;
  padding: 5px;
  font-size: .9rem;
  display: flex;
}

.load-more {
  border: 2px solid var(--primary-black);
  color: var(--accent-color);
  background-color: var(--primary-black);
  cursor: pointer;
  border-radius: 50px;
  outline: none;
  margin: auto auto 60px;
  padding: 7px 25px;
  font-size: 1.5rem;
}

.load-more:hover, .load-more:focus, .load-more:active {
  background-color: var(--accent-color);
  color: var(--primary-black);
}

.pending-btn {
  border: 2px solid var(--primary-black);
  color: var(--accent-color);
  background-color: var(--primary-black);
  cursor: pointer;
  border-radius: 50px;
  outline: none;
  align-items: center;
  gap: 10px;
  margin: auto auto 60px;
  padding: 10px 20px;
  font-size: 1.5rem;
  display: flex;
}

